.sb-dnd-container {
  appearance: none;
  background-color: var(--sbt-white);
  border: 1px solid var(--sbt-black-alpha-15);
  border-radius: var(--sbt-border-radius-large);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  font-size: var(--sbt-font-size-30);
  grid-auto-rows: max-content;
  margin: var(--sbt-spacing-30);
  min-height: 200px;
  min-width: 350px;
  outline: none;
  overflow: hidden;
  transition: background-color 350ms ease;

  div.sb-dnd-container__list {
    display: flex;
    flex-direction: column;
    list-style: none;
    margin: 0;
    padding: var(--sbt-spacing-50);

    div.sb-dnd-item-wrapper {
      margin-bottom: 14px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__scrollable {
    .sb-dnd-container__list {
      overflow-y: auto;
    }
  }

  &__placeholder {
    align-items: center;
    background-color: transparent;
    border-color: var(--sbt-gray-30);
    border-style: dashed;
    color: var(--sbt-gray-30);
    cursor: pointer;
    justify-content: center;

    &:hover {
      border-color: var(--sbt-gray-50);
    }
  }

  &__hover {
    background-color: var(--sbt-theme-color-20);
  }

  &__unstyled {
    background-color: transparent !important;
    border: none !important;
    overflow: visible;
  }

  &__horizontal {
    width: 100%;

    .sb-dnd-container__list {
      grid-auto-flow: column;
    }
  }

  &__shadow {
    box-shadow: 0 1px var(--sbt-spacing-30) 0 var(--sbt-black-alpha-15);
  }

  &:focus-visible {
    border-color: transparent;
    box-shadow: 0 0 0 2px var(--sbt-black-alpha-15),
      0 0 0 2px var(--sbt-theme-color-40);
  }

  &__header {
    align-items: center;
    background-color: var(--sbt-white);
    border-bottom: 1px solid var(--sbt-black-alpha-15);
    border-top-left-radius: var(--sbt-border-radius-large);
    border-top-right-radius: var(--sbt-border-radius-large);
    display: flex;
    justify-content: space-between;
    padding: var(--sbt-spacing-10) var(--sbt-spacing-50);
    padding-right: var(--sbt-spacing-20);

    &__actions {
      display: flex;
    }
  }
}
