@keyframes pop {
  0% {
    box-shadow: var(--box-shadow);
    transform: scale(1);
  }

  100% {
    box-shadow: var(--box-shadow-picked-up);
    transform: scale(var(--scale));
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.sb-dnd-item-wrapper {
  box-sizing: border-box;
  display: flex;
  touch-action: manipulation;
  transform: translate3d(var(--translate-x, 0), var(--translate-y, 0), 0)
    scaleX(var(--scale-x, 1)) scaleY(var(--scale-y, 1));
  transform-origin: 0 0;

  &__fadeIn {
    animation: fadeIn 500ms ease;
  }

  &__dragOverlay {
    box-shadow: var(--sbt-elevation-40);
    scale: 1.05;
    z-index: 999;
  }
}

.sb-dnd-item {
  align-items: center;
  background-color: var(--sbt-white);
  border-radius: calc(4px / var(--scale-x, 1));
  box-shadow: var(--sbt-elevation-40);
  box-sizing: border-box;

  color: var(--sbt-gray-80);
  display: flex;
  flex-grow: 1;
  font-size: var(--sbt-font-size-30);
  font-weight: var(--sbt-font-weight-40);
  list-style: none;
  outline: none;
  padding: var(--sbt-spacing-50);
  position: relative;

  -webkit-tap-highlight-color: transparent;

  transform: scale(var(--scale, 1));
  transform-origin: 50% 50%;
  transition: box-shadow 200ms cubic-bezier(0.18, 0.67, 0.6, 1.22);
  white-space: nowrap;

  &:focus-visible {
    box-shadow: 0 0 4px 1px var(--sbt-theme-color-40), var(--sbt-elevation-40);
  }

  &__dragging:not(.sb-dnd-item__dragOverlay) {
    opacity: var(--dragging-opacity, 0.5);
    z-index: 0;

    &:focus {
      box-shadow: var(--sbt-elevation-40);
    }
  }

  &__disabled {
    background-color: var(--sbt-color-disabled-background);
    color: var(--sbt-color-disabled-text);
    cursor: not-allowed;

    &:focus {
      box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.1), var(--sbt-elevation-40);
    }
  }

  &__dragOverlay {
    /* box-shadow: 0 0px 6px 2px var(--sbt-theme-color-40); */
    animation: pop 200ms cubic-bezier(0.18, 0.67, 0.6, 1.22);
    box-shadow: var(--box-shadow-picked-up);
    cursor: inherit;
    opacity: 1;
    transform: scale(var(--scale));
  }

  &__color::before {
    background-color: var(--color);
    border-bottom-left-radius: var(--sbt-border-radius-small);
    border-top-left-radius: var(--sbt-border-radius-small);
    content: '';
    display: block;
    height: 100%;
    left: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 3px;
  }

  &__remove {
    visibility: hidden;
  }

  &__actions {
    align-self: flex-start;
    display: flex;
    margin-bottom: -var(--sbt-spacing-30);
    margin-left: auto;
    margin-right: -var(--sbt-spacing-30);
    margin-top: -var(--sbt-spacing-30);
  }
}
